* {
  margin: 0;
  padding: 0;
}
.head {
  display: grid;
  background-color: lightblue;
  height: 150px;
  margin-top: 0;
  justify-content: center;
  align-items: center;
}
.head h1 {
  font-size: 50px;
  color: white;
}
.head input {
  width: auto;
  height: 40px;
  font-size: 20px;
  text-align: center;
}

.grid {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

h2 {
  text-align: center;
}
p {
  text-align: center;
}

img {
  width: 150px;
  height: 120px;
  margin-left: 35px;
}